.ShoppingCartControl_PersonifyDefault {
	.productControlSection {
		position: static;
		width: 100%;
	}
}

.segmentsListTabs .rlbSelected {
	background-color: $ift-blue !important;
	&:hover,
	&:active,
	&:focus {
		background-color: $ift-blue-dark;
	}
}

#ctl00_MainContent_ctl00_CheckoutItemSummaryControl_ShoppingCartSummaryControl_ButtonCouponApply {
	@include unButton()
}

.CheckoutControl_PersonifyDefault {
	.addressButtonsDiv {
		input {
			// font-size: 1rem;
			// background-color: $ift-blue;
			// background-image: none;
			@include unButton();
			padding: .8rem;
			margin-bottom: 0;
			+ input {
				display: block;
				margin-top: 1rem;
				margin-left: 0;
			}
		}
	}
}

// Warning: Attempts to mimic grid
.mainCheckoutDiv {
	box-sizing: border-box;
	display: flex;
	flex: 0 1 auto;
	flex-direction: row;
	flex-wrap: wrap;
	padding-bottom: $gutter-width;
	float: none;
	margin: 0;
	width: auto;
	min-width: 0;
	margin-left:-15px;
	margin-right:-15px;
	@include break(max, $breakpoint-max-mobile){
		display: block;
	}
	.itemsList,
	.summaryControl {
		box-sizing: border-box;
		flex: 0 0 auto;
		margin: 0;
		padding: 0;
		width: auto;
		float: none;
		margin-left:15px;
		margin-right:15px;
		flex-basis: 100%;
		max-width: 100%;
	}
	
	@media only screen and (min-width: $breakpoint-min-md) {
		.itemsList{
			flex-basis: calc(75% - 30px);
			max-width: calc(75% - 30px);
		}
		.summaryControl {
			flex-basis: calc(25% - 30px);
			max-width: calc(25% - 30px);
			.header {
				color: white;
				background: $ift-blue;
			}
		}
	}
}
#ctl00_MainContent_ctl01_ButtonEstimate{
	@include unButton();
	border:none;
}
.ShoppingCartSummaryControl_PersonifyDefault.ShoppingCart_PersonifyDefault.Base_PersonifyDefault{
		background-color: white;
		border: none;
		margin-top: 17px;
		.header {
			color: white;
			background: $ift-blue;
		}
		.summaryData{
			padding: 6px 10px;
		}
		.summaryData,
		.checkout-panel,
		.ShoppingCartSavingsControl_PersonifyDefault{
			border: none;
			background-color: white;
		}
	}
	.itemsList{
		.centerSection,
		.borderTop{
			border-color: #eee
		}
		.contentSection{
			border:none;
			background-color: white;

		}
	} 
div.WizardHeaderControl_PersonifyDefault{
	margin-bottom: 4rem;
	span {
		//personify override
		span {
			width: auto !important;
		}
	}
	span.step {
		background: transparent;
		border-color: $ift-grey-230;
		color: $ift-grey-medium-light;
		text-align: left;
		padding-left: 6rem !important;
		.step-number {
			width: 30px!important;
			height: 30px!important;
			border-radius: 100% !important;
			border: 1px solid white;
			box-shadow: none;
			position: absolute;
			left: 20px;
			text-shadow: none !important;
			top: 9px;
			padding: 5px 0;
			background-color: $ift-grey-medium-light;
		}
		.stepLabel {
			color: $ift-grey-medium-light;
		}
		&.active {
			background-color: $ift-green !important;
			border-bottom-color: $ift-green !important;
			.stepLabel {
				color: $white;
			}
			.step-number {
				background-color: transparent;
			}
		}
	}
}

@include break(min, $breakpoint-min-tablet) {
	//if 3, then 33%
	.wizardHeader .step:nth-last-child(n+3):nth-last-child(-n+3):first-child,
	.wizardHeader .step:nth-last-child(n+3):nth-last-child(-n+3):first-child ~ .step {
		width: 33%;
	}
	//if 4, then 25%
	.wizardHeader .step:nth-last-child(n+4):nth-last-child(-n+4):first-child,
	.wizardHeader .step:nth-last-child(n+4):nth-last-child(-n+4):first-child ~ .step {
		width: 24%;
	}
}

[id$="AddAddressControl_ContainerPanelPanel"] {
